@import "variables";

.Notes {
    @include module();
    @include flexbox($justifyContent: flex-start, $alignItems: flex-start);


    .notepad {
        width: 100%;
        height: 100%;
        padding-right: 2rem;
        padding-bottom: 4rem;

        span {
            margin-left: 2rem;
          }
    
        textarea {
            margin: 1rem;
            height: 100%;
            width: 100%;
            border-radius: $small-border-radius;
            border: none;
            padding: 1rem;
            font-family: $body-font;
            font-size: 1rem;
            resize: none;
            background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(255,255,255,0.1) 100%);

            &:focus {
                border: none;
                outline: none; 
            }
        }
    }
  

}